.i{
    display:flex;
    height: 100vh;
}

.i-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.i-right{
    flex: 1;
    position: relative;
}
.i-left-wrapper{
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    

}

.i-intro{
    font-size: 30px;
    font-weight: 300;
}

.i-name{
    font-size: 60px;
}

.i-title{
    height: 50px;
    overflow: hidden;
}

.i-title-wrapper{
    height: 100%;
    /* animation: move 10s ease-in-out infinite alternate; */
}

/* @keyframes move {
    100%{
        transform: translateY(-50px);
    }

} */

.i-scroll{
   width: 30px;
   height: 30px; 
   position: absolute;
   bottom: 20px;
}
.i-title-item{
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color:#212121;

    
    display: flex;
    align-items: center;
}


.i-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    
}

.i-bg{
    clip-path: circle(50% at 50% 50%);
    background-color: coral;
    width: 100%;
    height: 100%;
    position: absolute;
    top:0;
    right: 0;
}

@media screen and (max-width:480px) {
    .i{
        flex-direction: column;
    
        
    }

    .i-intro{
        font-size: 20px;
    }

    .i-name{
        font-size: 40px;
    }

    .i-title-item{
        font-size: 20px;
    }

    .i-left-wrapper{
        padding: 10px;
        align-items: center;
        text-align: center;
        justify-content: center;
        height: 100%;
        

    }

    .i-desc{
        display: none;
    }
    
}
